
#sloading[data-v-2d005368]{
}
.spinner[data-v-2d005368] {
  width: 60px;
  height: 60px;
 
  position: relative;
  margin: 100px auto;
}
.double-bounce1[data-v-2d005368], .double-bounce2[data-v-2d005368] {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #FFD440;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
   
  -webkit-animation: bounce-data-v-2d005368 2.0s infinite ease-in-out;
  animation: bounce-data-v-2d005368 2.0s infinite ease-in-out;
}
.double-bounce2[data-v-2d005368] {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
@-webkit-keyframes bounce {
0%, 100% { -webkit-transform: scale(0.0)
}
50% { -webkit-transform: scale(1.0)
}
}
@keyframes bounce-data-v-2d005368 {
0%, 100% {
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
}
50% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
}
}




